body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mapCard {
  border: 1px solid black;
  border-radius: 5px;
  background-color: #FAFAFA;
  box-shadow: 2px 2px 5px #EEE;
  padding: 0;
  width: 24%;
  min-width: 400px;
}

.imagePart{
  position: relative;
  display: inline-block;
}

.cardImg {
  width: 100%
}

.cardBody {
  position: relative;
  padding: 15px;
}

.vote {
  font-size: 25px;
}

.voteT {
  font-size: 22px;
}

.detDL {
  font-size: 20px;
}

.mapHoverOverlay {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity:0;
  -webkit-transition: 0.5s ease; /* Safari */
  transition: .2s ease;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 15px;
  color: white;
}

.mapCard:hover .mapHoverOverlay {
  opacity: 1
}

.updatedText {
  position: absolute;
  top: 90%;
}
